body {
  background: url('https://images.hdqwalls.com/wallpapers/the-riddler-fx.jpg')
    center/cover repeat;
}
.container {
  margin: 50px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.box {
  border: 2px solid rgba(255, 0, 0, 0);
  padding: 30px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.634);
  box-shadow: 8px 8px 3px rgba(255, 255, 255, 0.456);
}

p {
  font-size: 2rem;
}

img {
  border: 2px solid rgba(255, 255, 255, 0);
  border-radius: 10px;
  cursor: pointer;
}
